[class^="uxr-select-"]{box-sizing:border-box}.uxr-select-wrap{position:relative}.uxr-select-wrap .uxr-select-aria-hidden{position:absolute;overflow:hidden;width:1px;height:1px;margin:-1px;padding:0;border:0;clip:rect(0, 0, 0, 0);-webkit-clip-path:inset(0 0 0 0);clip-path:inset(0 0 0 0)}.uxr-select-ready{visibility:hidden}.uxr-select-selection{position:relative;display:inline-block;height:32px;border-radius:4px;border:1px solid #ccc;color:#222;text-decoration:none;vertical-align:middle}.uxr-select-selection.uxr-select-opened{border-radius:4px 4px 0 0}.uxr-select-selection-text{display:block;overflow:hidden;width:100%;padding:5px 30px 5px 5px;text-overflow:ellipsis;white-space:nowrap}.uxr-select-arrow{position:absolute;top:50%;right:5px;width:0;height:0;margin-top:-2px;border:5px solid;border-color:#555 transparent transparent}.uxr-select-multiple .uxr-select-arrow{display:none}.uxr-select-opened .uxr-select-arrow{margin-top:-7px;border-color:transparent transparent #555}.uxr-select-drop{position:absolute;margin-top:30px;border:1px solid #ccc;border-radius:0 0 4px;background-color:#fff}.uxr-select-list{max-height:250px;overflow-y:scroll;margin:0;padding:0;list-style:none}.uxr-select-search{display:block;padding:4px}.uxr-select-search input{width:calc(100% - 8px);padding:4px;border:1px solid #ccc;border-radius:4px}.uxr-select-option{display:block;padding:4px;cursor:pointer}

